import back from '@/components/back'

export default {
    components:{
        back
    },
	data () { //引入PopupPicker组件 引入提现路由
		return {
			DrId: this.$route.params.id,
			cardNull: false,
			CardNo: '', //卡号
			Name: '', //户主
			BankName: '',//银行名称，
			OpenBank: '',//开户行
			DrBankCardId: null
		}
	},
	methods: {
		getCard () {
			this.$get('/PlatFormAPI/Doctor/QueryDoctorBankCardByDrId',{
				DrId : this.DrId
			}).then(o => {
				if(o != null && o != undefined && o != ''){
					this.cardNull = true
					this.Name = o.Name
					this.CardNo = this.formatBankNumber(o.CardNo)
					this.BankName = o.BankName || '暂无数据'
					this.OpenBank = o.OpenBank || '暂无数据'
					this.DrBankCardId = o.DrBankCardId
				}
			})
		},
		nameTransitionFn (o) {
			if(o == null || o == undefined){
				return '暂无数据'
			}else{
				let n = ''
				for(let i in o){
					i == 0 ? n += o[i] : n += '*'
				}
				return n
			}
		},
		formatBankNumber (o) {
			if(o == null || o == undefined){
				return '暂无数据'
			}else{
				return o.substr(0,4) + '********' + o.substr(-4)
			}
		},
		addCard () {
			this.$router.push({
				path: '/Center/addCard',
				name: 'addCard',
				params: {
					id: this.DrId,
					start: 1, //新增
				}
			})
		},
		updataCard () {
			this.$router.push({
				path: '/Center/addCard',
				name: 'addCard',
				params: {
					id: this.DrId,
					start: 2, //修改
				}
			})
		}
	},
	created(){
		this.getCard()
	}
}



